function handleLanguageChange() {
  const getLangValue = localStorage.getItem(langKey);  
  const arrayToCompare = Object.entries(translates[getLangValue]);
  arrayToCompare.forEach( function (item) {
    const key = item[0];
    const text = item[1];
    const $element = document.getElementById(key);
    if ($element) {
      $element.innerText = text;
    }
  });
};

function setLanguageCookie(langValue){
  const cookieLangValue = localStorage.getItem(langKey);
  if (langValue) {
    localStorage.setItem(langKey, langValue);
  } else {
    if (!cookieLangValue) {
      localStorage.setItem(langKey, supportedLanguages[0]);
    }
  }
}

function setLanguageFromTrigger(selectElement) {
  const value = selectElement.value;  
  setLanguageCookie(value);
  handleLanguageChange();
};

function renderSelectedNavbar() {
  const selectedEn = localStorage.getItem(langKey) === 'en';
  const selectedEs = localStorage.getItem(langKey) === 'es';
  const $WrapperSelect = document.getElementById('WrapperSelect');
  
  const selectElement = `
  <select class="form-control" name="select" onchange="setLanguageFromTrigger(this)">
    <option value="en" ${selectedEn && "selected"}>üá¨üáß English</option>
    <option value="es" ${selectedEs && "selected"}>üá¶üá∑ Espa√±ol</option>
  </select>`;

  $WrapperSelect.innerHTML = selectElement;
};

setLanguageCookie();
renderSelectedNavbar();
